import { Datagrid } from '@multiverses/verse-react/src/components/Datagrid';
import PropTypes from 'prop-types';

const schema = {
  columns: [
    {
      dataKey: 'message',
      title: 'Message',
      width: 160,
      dataType: 'String',
    },
  ],
};

export const Logs = ({ logs }) => {
  return (
    <div>
      <Datagrid data={logs} height={220} schema={schema} title="Logs" />
    </div>
  );
};

Logs.propTypes = {
  logs: PropTypes.arrayOf(PropTypes.shape({})),
};

Logs.defaultProps = {
  logs: [],
};
