import { Form } from '@multiverses/verse-react/src/components/Form';
import PropTypes from 'prop-types';

import { useModel } from '../../../../../../verse-react/src/utility/hooks';
import { useApp } from '../../../../AppProvider';

export const Quantity = ({ model, optionKey }) => {
  const finity = useModel('finity', {});
  const { quantity = {} } = useApp();
  console.log('##', quantity, quantity[finity[optionKey]]);
  return (
    <Form.Input
      defaultValue={quantity[finity[optionKey]] || 50}
      label="Quantity"
      model={model}
      placeholder="Quantity"
      type="number"
    />
  );
};

Quantity.propTypes = {
  model: PropTypes.string,
  optionKey: PropTypes.string,
};

Quantity.defaultProps = {
  model: 'finity.quantity',
  trackStockCode: 'stockCode',
};
