import { useMemo } from 'react';

import PropTypes from 'prop-types'; 

import defaultRenderer from './utils/defaultRenderer';
import './css/sidebar.scss';

export const SideBar = ({ contents, renderer, isLink }) => {
  const sideBarContents = useMemo(() => {
    return renderer(contents, isLink);
  }, [renderer]);

  return <div className="sidebar">{sideBarContents}</div>;
};

SideBar.propTypes = {
  contents: PropTypes.shape({}),
  isLink: PropTypes.bool,
  renderer: PropTypes.func,
};

SideBar.defaultProps = {
  contents: {},
  isLink: false,
  renderer: defaultRenderer,
};
