import { Navbar } from '@multiverses/verse-react/src/components/Navigation/Navbar';
import { SideBar } from '@multiverses/verse-react/src/components/SideBar';

import { contents } from './apps';
import { Contents } from './contents';

function App() {
  return (
    <div className="light-theme">
      <Navbar>
        <Navbar.NavItem>
          <b className="navitem-title">MultiVerse</b>
        </Navbar.NavItem>
      </Navbar>{' '}
      <div className="flex h-100VH">
        <div className="flex-10 primary">
          <SideBar contents={contents} isLink={true} />
        </div>
        <div className="flex-90">
          <Contents />
        </div>
      </div>
    </div>
  );
}

export default App;
