import React, { useState } from 'react';

import PropTypes from 'prop-types';
import Draggable from 'react-draggable';

import { Button } from '../../components/Button';

export const Expandable = ({
  showContent,
  children,
  title,
  isFixed,
  position,
  onClick,
  onClose,
}) => {
  const [activeDrags, setActiveDrags] = useState(0);
  const [show, setShow] = useState(showContent);
  const onStart = () => {
    const positiveDrag = activeDrags + 1;
    setActiveDrags(positiveDrag);
  };

  const onStop = () => {
    const positiveDrag = activeDrags - 1;
    setActiveDrags(positiveDrag);
  };
  const dragHandlers = { onStart, onStop };

  const onShow = () => {
    setShow(!show);
    onClick();
  };

  const handleClose = () => {
    setShow(false);
    onClose();
  };

  return (
    <React.Fragment>
      <div className={isFixed ? position : ''}>
        <Button onClick={onShow}>{title}</Button>
      </div>
      <div className={!show && 'hide'}>
        <Draggable {...dragHandlers}>
          <div className={`section move ${position}`}>
            <div className="flex">
              <div className="flex-80 m-x-16">
                <b>{title}</b>
              </div>
              <div className="flex-20">
                <span className="close" onClick={handleClose}>
                  &times;
                </span>
              </div>
            </div>
            <div>{children}</div>
          </div>
        </Draggable>
      </div>
    </React.Fragment>
  );
};

Expandable.propTypes = {
  children: PropTypes.node.isRequired,
  isFixed: PropTypes.bool,
  onClick: PropTypes.func,
  onClose: PropTypes.func,
  position: PropTypes.oneOf([
    'top-right',
    'top-left',
    'bottom-right',
    'bottom-left',
  ]),
  showContent: PropTypes.bool,
  title: PropTypes.node,
};

Expandable.defaultProps = {
  isFixed: false,
  onClick: () => null,
  onClose: () => null,
  position: 'bottom-right',
  showContent: false,
  title: '',
};
