import React from 'react';

import { ExpiryDate, Right, StrikePrice } from '../../../FormFields';

export const OptionForm = () => {
  return (
    <React.Fragment>
      <div className="flex space-btw m-t-8">
        <div className="flex-45">
          <Right model="finity.trackRight" />
        </div>
        <div className="flex-45">
          <StrikePrice model="finity.trackStrikePrice" />
        </div>
      </div>
      <div className="flex space-btw m-t-8">
        <ExpiryDate model="finity.trackExpiryDate" />
      </div>
    </React.Fragment>
  );
};
