import {
  useApi,
  useApiHelpers,
  useApiState,
} from '@multiverses/verse-react/src/utility/redux/slices/api/apiSlice';
import { localStorage } from '@multiverses/verse-react/src/utility/storage';

const ICICI_BREEZE_V1 = 'http://localhost:7001';

export const apiRoutes = {
  login: {
    url: `${ICICI_BREEZE_V1}/login`,
    method: 'POST',
  },
  funds: {
    url: `${ICICI_BREEZE_V1}/funds`,
    method: 'GET',
  },
  setFunds: {
    url: `${ICICI_BREEZE_V1}/funds`,
    method: 'POST',
  },
  portfolio: {
    url: `${ICICI_BREEZE_V1}/portfolio`,
    method: 'POST',
    params: ({ data }) => {
      return {
        data: {
          exchangeCode: data.trackExchange || data.exchangeCode,
        },
      };
    },
  },
  gnews: {
    url: `${ICICI_BREEZE_V1}/news/gnews/sentiment`,
    method: 'POST',
  },
  history: {
    url: `${ICICI_BREEZE_V1}/historicalcharts/v2`,
    method: 'POST',
    cache: true,
    params: ({ data }) => {
      return {
        data: {
          interval: data.interval,
          fromDate: `${data.fromDate}:00.000Z`,
          toDate: `${data.toDate}:00.000Z`,
          stockCode: data.stockCode.toUpperCase(),
          exchangeCode: data.exchangeCode,
          productType: data.product,
          expiryDate: data.expiryDate,
          right: data.right,
          strikePrice: data.strikePrice,
        },
      };
    },
  },
  quotes: {
    url: `${ICICI_BREEZE_V1}/quotes`,
    method: 'POST',
    params: ({ data }) => {
      return {
        data: {
          stockCode: data.trackStockCode
            ? data.trackStockCode.toUpperCase()
            : data.stockCode.toUpperCase(),
          exchangeCode: data.trackExchange || data.exchangeCode,
          expiryDate: data.trackExpiryDate || data.expiryDate,
          productType: data.trackProduct || data.productType,
          right: data.trackRight || data.right,
          strikePrice: data.trackStrikePrice || data.strikePrice,
        },
      };
    },
  },
  order: {
    url: `${ICICI_BREEZE_V1}/order`,
    method: 'POST',
    params: ({ data }) => {
      return {
        data: {
          stockCode: data.trackStockCode
            ? data.trackStockCode.toUpperCase()
            : data.stockCode.toUpperCase(),
          exchangeCode: data.trackExchange || data.exchangeCode,
          product: data.trackProduct || data.productType,
          action: 'buy',
          orderType: data.orderType,
          stoploss: data.stoploss,
          quantity: data.quantity,
          price: data.price,
          validity: 'day',
          validityDate: data.validityDate,
          disclosedQuantity: data.disclosedQuantity,
          expiryDate: data.expiryDate,
          right: data.right,
          strikePrice: data.strikePrice,
          userRemark: data.userRemark,
        },
      };
    },
  },
  orderList: {
    url: `${ICICI_BREEZE_V1}/order/list`,
    method: 'POST',
    params: ({ data }) => {
      return {
        data: {
          exchangeCode: data.trackExchange || data.exchangeCode,
        },
      };
    },
  },
  orderDelete: {
    url: `${ICICI_BREEZE_V1}/order/delete`,
    method: 'POST',
    params: ({ data }) => {
      return {
        data: {
          exchangeCode: data.exchange_code,
          orderId: data.order_id,
        },
      };
    },
  },
  trades: {
    url: `${ICICI_BREEZE_V1}/trades`,
    method: 'POST',
    params: ({ data }) => {
      return {
        data: {
          exchangeCode: data.trackExchange || data.exchangeCode,
          order_id: data.orderId,
        },
      };
    },
  },
  optionchain: {
    url: `${ICICI_BREEZE_V1}/optionchain`,
    method: 'POST',
    params: ({ data }) => {
      return {
        data: {
          exchangeCode: data.trackExchange || data.exchangeCode,
          stockCode: data.stockCode.toUpperCase(),
          expiryDate: `${data.expiryDate}T06:00:00.000Z`,
          productType: data.product,
          right: data.right,
          strikePrice: data.strikePrice,
        },
      };
    },
  },
};

export const useFinityApi = (apiName, options) => {
  const apiAction = useApi(apiName, apiRoutes[apiName], options);
  const apiState = useApiState(apiName);
  const apiHelpers = useApiHelpers(apiName);

  return {
    [`${apiName}State`]: apiState,
    [`${apiName}Request`]: data => {
      let breezeCount = localStorage.get('BREEZE_API_CALL_COUNT') || 0;
      if (breezeCount < 5000) {
        localStorage.set('BREEZE_API_CALL_COUNT', breezeCount + 1);
      } else {
        alert('API_LIMIT_REACHED');
        return;
      }

      const newData = apiRoutes[apiName].params
        ? apiRoutes[apiName].params(data)
        : data;
      return apiAction(newData);
    },
    ...apiHelpers,
  };
};
