import { Form } from '@multiverses/verse-react/src/components/Form';
import localDateTime from '@multiverses/verse-utility/date/localDateTime';
import PropTypes from 'prop-types';

export const FromDate = ({ model }) => {
  const date = localDateTime('9:20');
  return (
    <Form.Datepicker
      defaultValue={date}
      label="From Date"
      model={model}
      placeholder="From Date"
    />
  );
};

FromDate.propTypes = {
  model: PropTypes.string,
};

FromDate.defaultProps = {
  model: 'finity.fromDate',
};
