import { Clock } from '@multiverses/verse-react/src/components/Clock';
import { Skeleton } from '@multiverses/verse-react/src/components/Skeleton';
import PropTypes from 'prop-types';

export const LiveStatus = ({ liveData }) => {
  return (
    <div className="live-result-card-left primary">
      <div className="m-x-8">
        <div className="flex space-btw flex-center">
          <div className="flex-60">
            <Skeleton keyName="">{liveData.close || 'Paused'}</Skeleton>
          </div>
          <div className="flex-40">
            <Clock paused={!liveData.close} />
          </div>
        </div>
      </div>
    </div>
  );
};

LiveStatus.propTypes = {
  liveData: PropTypes.shape({
    close: PropTypes.number,
  }),
};

LiveStatus.defaultProps = {
  liveData: {},
};
