import { Login as LoginVerse } from '@multiverses/verse-react/src/components/Login';

import { useFinityApi } from '../../../hooks/useFinityApi';

export const Login = () => {
  const { loginRequest } = useFinityApi('login');

  const onLogin = async data => {
    const loginResponse = await loginRequest({
      data,
    });
    console.log('BreezeConnect', loginResponse);
    console.log('BreezeConnect2', loginResponse);
  };

  return (
    <div className="flex-100 m-t-32">
      <div className="flex flex-center">
        <div className="flex-50">
          <div className="text-center">
            <b>Finity</b>
          </div>
        </div>
        <div className="flex-50">
          <div>
            <LoginVerse
              onLogin={onLogin}
              tfa={{
                label: 'DOB',
                model: 'dob',
                type: 'password',
                placeholder: 'Date of Birth',
              }}
            />
          </div>
        </div>
      </div>
    </div>
  );
};
