import React from 'react';

import { Datagrid } from '@multiverses/verse-react/src/components/Datagrid';
import { Skeleton } from '@multiverses/verse-react/src/components/Skeleton';
import { Text } from '@multiverses/verse-react/src/components/Text';
import PropTypes from 'prop-types';

const schema = {
  columns: [
    {
      dataKey: 'buyTime',
      title: 'Buy Date / Time',
      width: 160,
      dataType: 'String',
    },
    {
      dataKey: 'buyPrice',
      title: 'Buy Price',
      width: 80,
      dataType: 'String',
    },
    {
      dataKey: 'sellTime',
      title: 'Sell Date / Time',
      width: 160,
      dataType: 'String',
    },
    {
      dataKey: 'sellPrice',
      title: 'Sell Price',
      width: 80,
      dataType: 'String',
    },
    {
      dataKey: 'breakEvenPoint',
      title: 'BEP',
      width: 80,
      dataType: 'String',
    },
    {
      dataKey: 'acquiredPts',
      title: 'Acquired Pts',
      width: 80,
      dataType: 'String',
    },
    {
      dataKey: 'pnlBeforeExpense',
      title: 'EBE',
      width: 80,
      dataType: 'String',
    },
    {
      dataKey: 'totalExpense',
      title: 'Total Expense',
      width: 80,
      dataType: 'String',
    },
    {
      dataKey: 'pnl',
      title: 'PnL',
      width: 80,
      dataType: 'String',
    },
  ],
};

export const Orders = ({ results }) => {
  return (
    <div>
      <Datagrid
        data={results.orders}
        height={220}
        schema={schema}
        title={
          <React.Fragment>
            <div className="flex-15">
              <Skeleton keyName="EBE" title="PNL before expenses">
                <Text format="currency_inr">{results.pnlBeforeExpense}</Text>
              </Skeleton>
            </div>
            <></>
            <div className="flex-15">
              <Skeleton keyName="Charges">
                <Text format="currency_inr">{results.charges}</Text>
              </Skeleton>
            </div>
            <div className="flex-15">
              <Skeleton keyName="Orders">{results.orderCount}</Skeleton>
            </div>
            <div className="flex-15">
              <Skeleton keyName="Winners">{results.winCount}</Skeleton>
            </div>
            <div className="flex-15">
              <Skeleton keyName="Losers">{results.loseCount}</Skeleton>
            </div>
            <div className="flex-15">
              <Skeleton keyName="Win %">{results.winPct}</Skeleton>
            </div>
            <div className="flex-15">
              <Skeleton keyName="PnL">
                <Text format="currency_inr">{results.pnl}</Text>
              </Skeleton>
            </div>
          </React.Fragment>
        }
      />
    </div>
  );
};

Orders.propTypes = {
  results: PropTypes.shape({
    buyCount: PropTypes.number,
    charges: PropTypes.number,
    comboName: PropTypes.string,
    loseCount: PropTypes.number,
    orderCount: PropTypes.number,
    orders: PropTypes.arrayOf(PropTypes.shape({})),
    pnl: PropTypes.number,
    pnlBeforeExpense: PropTypes.number,
    sellCount: PropTypes.number,
    winCount: PropTypes.number,
    winPct: PropTypes.number,
  }),
};

Orders.defaultProps = {
  orders: [],
};
