import { useEffect } from 'react';

import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';

import { useModel } from '../../utility/hooks';
import { useForm } from '../../utility/redux/slices/forms/formSlice';

export const ButtonGroup = ({ groups, model, activeGroup }) => {
  const value = useModel(model);
  const { set } = useForm();

  const handleClick = e => {
    set({ model, value: e });
  };

  useEffect(() => {
    if (activeGroup && value !== activeGroup) {
      handleClick(activeGroup);
    }
  }, [activeGroup]);

  return (
    <For each="group" index="idx" of={groups}>
      <Choose>
        <When condition={group === value}>
          <NavLink
            className="radio-flat secondary"
            key={idx}
            onClick={() => handleClick(group)}
            to={group.toLowerCase().replace(/ /g, '')}
          >
            {group}
          </NavLink>
        </When>
        <Otherwise>
          <NavLink
            className="radio-flat secondary-outline"
            key={idx}
            onClick={() => handleClick(group)}
            to={group.toLowerCase().replace(/ /g, '')}
          >
            {group}
          </NavLink>
        </Otherwise>
      </Choose>
    </For>
  );
};

ButtonGroup.propTypes = {
  model: PropTypes.string.isRequired,
  activeClass: PropTypes.string,
  activeGroup: PropTypes.string,
  groups: PropTypes.arrayOf(PropTypes.string),
  inactiveClass: PropTypes.string,
  onClick: PropTypes.func,
};

ButtonGroup.defaultPropTypes = {
  groups: [],
  activeGroup: '',
  activeClass: 'radio secondary',
  inactiveClass: 'radio secondary-outline',
};
