import { useEffect, useState } from 'react';

import { Button } from '@multiverses/verse-react/src/components/Button';
import { PlayPause } from '@multiverses/verse-react/src/components/PlayPause';
import { useModel } from '@multiverses/verse-react/src/utility/hooks';

import useBreeezeSocket from '../../../hooks/useBreezeSocket';
import { useApp } from '../../AppProvider';
import { LiveResults } from '../../component/LiveResults';
import { brokerage } from '../../functions/brokerage';
import { strategiesList } from '../../resources/strategies/live';
import {
  ExchangeCode,
  ExpiryDate,
  Product,
  Right,
  StockCode,
  StrikePrice,
} from '../common/FormFields';
import { StrategyList } from '../common/FormFields/StrategyList';

export const LiveTest = () => {
  const [baseData, setBaseData] = useState();
  const [trackData, setTrackData] = useState();
  const [logs, setLogs] = useState([]);
  const [results, setResults] = useState({});
  const finity = useModel('finity');
  const { finityDB } = useApp();

  const baseCB = async cbData => {
    await addHistory(cbData);
    setBaseData(cbData);
  };

  const trackCB = async cbData => {
    setTrackData(cbData);
  };

  const { baseWatch, trackWatch, disconnect, isConnected } = useBreeezeSocket(
    baseCB,
    trackCB
  );

  const handleLogs = async () => {
    if (finityDB.logs) {
      const logs = await finityDB.logs.getAll();
      setLogs(logs.reverse());
    }
  };

  const handleOrders = async () => {
    if (finityDB.orders) {
      const orders = await finityDB.orders.getAll();
      const results = brokerage('options', orders);
      setResults({
        ...results,
        orders: results.orders.reverse(),
        tableOrders: orders.reverse(),
      });
    }
  };

  const addLog = message => {
    finityDB.logs.add({
      stockCode: finity.stockCode,
      interval: finity.interval,
      datetime: Date.now(),
      message,
    });
  };

  const addOrders = async (type, candle, processIndex) => {
    await finityDB.orders.add({
      stockCode: finity.stockCode,
      interval: finity.interval,
      datetime: candle.datetime,
      price: candle.close,
      type,
      quantity: finity.quantity,
      processIndex,
      progressState: 100,
    });
  };

  const addHistory = async data => {
    await finityDB.history.add({
      stockCode: data.stock_code,
      interval: data.interval,
      datetime: data.datetime,
      close: data.close,
    });
  };

  const buyOrder = async processIndex => {
    console.log('Buy Candle :::: ', trackData);
    const buyCandle = {
      close: trackData.close,
      datetime: trackData.datetime,
    };
    await addOrders('Buy', buyCandle, processIndex);
    handleOrders();
    await addLog(`Buy for ${buyCandle.close} @ ${buyCandle.datetime}`);
    handleLogs();
  };

  const sellOrder = async processIndex => {
    console.log('Sell Candle :::: ', trackData);
    const sellCandle = {
      close: trackData.close,
      datetime: trackData.datetime,
    };
    await addOrders('Sell', sellCandle, processIndex);
    handleOrders();
    await addLog(`Sell for ${sellCandle.close} @ ${sellCandle.datetime}`);
    handleLogs();
  };

  const onLiveDataChange = async () => {
    console.log(finityDB);
    const baseSet = (await finityDB.history.getAll()) || [];
    console.log('Base Set ::::: ', baseSet);
    const savedOrders = (await finityDB.orders.getAll()) || [];
    const lastOrder = savedOrders[savedOrders.length - 1];
    const hasBuyOrder = lastOrder?.type === 'Buy';
    console.log('Last Order ::::: ', lastOrder);
    const processIndex = lastOrder?.processIndex + 1 || 0;
    strategiesList[finity.strategy].calculation({
      baseSet,
      savedOrders,
      buyOrder,
      sellOrder,
      hasBuyOrder,
      processIndex,
    });
  };

  useEffect(() => {
    async function onLiveData() {
      if (finityDB.history) {
        onLiveDataChange();
      }
    }
    onLiveData();
  }, [baseData]);

  useEffect(() => {
    async function getAllLogs() {
      handleLogs();
    }
    getAllLogs();
  }, [finityDB.logs]);

  useEffect(() => {
    async function getAllOrders() {
      handleOrders();
    }
    getAllOrders();
  }, [finityDB.orders]);

  const onLiveRunStart = () => {
    // Start Breeze Socket
    console.log('Start Watch');
    baseWatch('4.1!NIFTY 50');
    trackWatch('4.1!54053');
  };

  const onLiveRunStop = () => {
    // Stop Breeze Socket
    disconnect();
  };

  return (
    <div className="flex space-btw">
      <div className="flex-20 p-r-8">
        <div className="flex">
          <div className="flex-80">
            <StrategyList />
          </div>
          <div className="flex-20">
            <div className="flex flex-center">
              <div className="m-t-28">
                <PlayPause play={isConnected} />
              </div>
            </div>
          </div>
        </div>
        <div className="flex space-btw m-t-8">
          <div className="flex-45">
            <Button className="btn error" onClick={onLiveRunStop}>
              Stop
            </Button>
          </div>
          <div className="flex-45">
            <Button
              className="btn success"
              // isLoading={historyState.loading}
              onClick={onLiveRunStart}
            >
              Start
            </Button>
          </div>
        </div>
        <div className="flex space-btw m-t-8">
          <div className="flex-45">
            <ExchangeCode defaultValue="NFO" model="finity.trackExchange" />
          </div>
          <div className="flex-45">
            <Product defaultValue="options" model="finity.trackProduct" />
          </div>
        </div>
        <div className="m-t-8">
          <StockCode model="finity.trackStockCode" />
        </div>
        <div className="flex space-btw m-t-8">
          <div className="flex-45">
            <Right model="finity.trackRight" />
          </div>
          <div className="flex-45">
            <StrikePrice model="finity.trackStrikePrice" />
          </div>
        </div>
        <div className="flex space-btw m-t-8">
          <ExpiryDate model="finity.trackExpiryDate" />
        </div>
      </div>
      <div className="flex-80">
        <LiveResults liveData={baseData} logs={logs} results={results} />
      </div>
    </div>
  );
};
