import { useMemo } from 'react';

import { Accordion } from '@multiverses/verse-react/src/components/Accordion';
import { ButtonGroup } from '@multiverses/verse-react/src/components/ButtonGroup';
import { Navbar } from '@multiverses/verse-react/src/components/Navigation/Navbar';
import {
  Route,
  Routes,
  useLocation,
} from '@multiverses/verse-react/src/components/Route';

import { Execute } from '../../widgets/Execute';
import { BackTest } from './BackTest';
import { Book } from './Book';
import { Dashboard } from './Dashboard';
import { Funds } from './Funds';
import { IDBManager } from './IDBManager';
import { LiveTest } from './LiveTest';
import { OptionChain } from './OptionChain';
import { Rewards } from './Rewards';
import { RunStrategy } from './RunStrategy';

export const Home = () => {
  const location = useLocation();

  const currentPath = useMemo(() => {
    let lastPath = location.pathname.slice(
      location.pathname.lastIndexOf('/') + 1,
      location.pathname.length
    );
    return lastPath.charAt(0).toUpperCase() + lastPath.slice(1);
  }, [location.pathname]);

  return (
    <div>
      <Navbar>
        <Navbar.NavItem>
          <b className="navitem-title">Finity</b>
        </Navbar.NavItem>
        <Navbar.NavItemRight>
          <div className="flex space-btw">
            <Execute />
          </div>
        </Navbar.NavItemRight>
      </Navbar>
      <div className="m-x-4 p-x-4">
        <Accordion model="sample-accordion">
          <Accordion.Wrapper accordionId="first-accordion">
            <Accordion.Header>
              <ButtonGroup
                activeGroup={currentPath}
                groups={[
                  'Dashboard',
                  'Backtest',
                  'Livetest',
                  'Execute',
                  'Book',
                  'Funds',
                  'Rewards',
                  'Option Chain',
                  'IDB Manager',
                ]}
                model="finity.choice"
              />
            </Accordion.Header>
            <Accordion.Content>Additional Options</Accordion.Content>
          </Accordion.Wrapper>
        </Accordion>
      </div>
      <div className="m-l-16 m-r-16 p-l-8 p-r-8">
        <Routes>
          <Route element={<Dashboard />} path="/" />
          <Route element={<Dashboard />} path="/dashboard" />
          <Route element={<BackTest />} path="/backtest" />
          <Route element={<LiveTest />} path="/livetest" />
          <Route element={<RunStrategy />} path="/runstrategy" />
          <Route element={<Book />} path="/book" />
          <Route element={<Funds />} path="/funds" />
          <Route element={<Rewards />} path="/rewards" />
          <Route element={<OptionChain />} path="/optionchain" />
          <Route element={<IDBManager />} path="/idbmanager" />
        </Routes>
      </div>
    </div>
  );
};
