import { useEffect, useState } from 'react';

import { finityDB } from '../idb';
import { AppProvider } from './AppProvider';
import { Contents } from './Contents';
import { getStock } from './functions/stock';

function App() {
  const [stockData, setStockData] = useState({});
  const breeze = {};
  const initStockData = async () => {
    const scripts = await getStock();
    setStockData(scripts);
  };

  useEffect(() => {
    async function fetchStocks() {
      await initStockData();
      await finityDB.init();
    }
    fetchStocks();
  }, []);

  return (
    <AppProvider value={{ breeze, finityDB, ...stockData }}>
      <Contents />
    </AppProvider>
  );
}

export default App;
