import { NavLink } from 'react-router-dom';

const defaultRenderer = (contents, isLink) => {
  const Wrapper = isLink ? NavLink : 'div';
  return (
    <div>
      <For each="content" of={Object.keys(contents)}>
        <Wrapper
          className="btn-block primary"
          key={content}
          to={content.toLowerCase()}
        >
          {content}
        </Wrapper>
      </For>
    </div>
  );
};

export default defaultRenderer;
