import PropTypes from 'prop-types';

export const Skeleton = ({ keyName, title, children }) => {
  return (
    <>
      <div className="flex-100 m-t-4 m-b-4" title={title || keyName}>
        <span className="f-1">{keyName}</span>
      </div>
      <div className="flex-100 fb-700 f-1">{children}</div>
    </>
  );
};

Skeleton.propTypes = {
  keyName: PropTypes.any.isRequired,
  children: PropTypes.node,
  title: PropTypes.string,
};

Skeleton.defultProps = {
  children: '',
};
