function getCookie(name) {
  const value = `; ${document.cookie}`;
  const parts = value.split(`; ${name}=`);
  if (parts.length === 2) {
    return parts.pop().split(';').shift();
  }
  return '';
}

// Base 64 session_token from customer details API response ( Set as cookie in my API code for reusability )
const sessionToken = getCookie('session_token').replace('%3D', '=');

// Parse token to ascii string
const parsedToken = Buffer.from(sessionToken, 'base64').toString('ascii');

// Parsed session_token is split into two parts with colon as separator
const splitToken = parsedToken.split(':');

