import React from 'react';

import { useModel } from '@multiverses/verse-react/src/utility/hooks';

import { ExchangeCode, Product, Quantity, StockCode } from '../../FormFields';
import { FuturesTrackForm } from '../FuturesForm';
import { OptionTrackForm } from '../OptionForm';

export const TrackForm = () => {
  const finity = useModel('finity');
  return (
    <React.Fragment>
      <div className="flex space-btw m-t-8">
        <div className="flex-45">
          <ExchangeCode defaultValue="NFO" model="finity.trackExchange" />
        </div>
        <div className="flex-45">
          <Product defaultValue="options" model="finity.trackProduct" />
        </div>
      </div>
      <div className="flex space-btw m-t-8">
        <div className="flex-60">
          <StockCode model="finity.trackStockCode" optionKey="trackExchange" />
        </div>
        <div className="flex-35">
          <Quantity model="finity.trackQty" optionKey="trackStockCode" />
        </div>
      </div>
      <Choose>
        <When condition={['options'].includes(finity.trackProduct)}>
          <OptionTrackForm />
        </When>
        <When condition={['futures'].includes(finity.trackProduct)}>
          <FuturesTrackForm />
        </When>
      </Choose>
    </React.Fragment>
  );
};
