const localDateTime = function (time = '00:00', d = new Date()) {
  var z = `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(
    2,
    '0'
  )}-${String(d.getDate() - 2).padStart(2, '0')}T${String(time).padStart(
    5,
    '0'
  )}`;
  return z;
};

export default localDateTime;
