import { Progress } from '@multiverses/verse-react/src/components/Progress';
import { Skeleton } from '@multiverses/verse-react/src/components/Skeleton';
import { Text } from '@multiverses/verse-react/src/components/Text';
import PropTypes from 'prop-types';

const status = {
  10: 'Sending Buy Order to exchange',
  25: 'Order sent to exchange',
  50: 'Order fullfillment in progress',
  100: 'Order fullfilment successful',
  0: 'Order cancelled by exchange',
  '-10': 'Order fullfillment failed, canceling buy order',
  undefined: 'Progress status is unavailable.',
};

const orderClass = {
  Buy: 'info',
  Sell: 'error',
};

export const OrderStatus = ({ results }) => {
  const { tableOrders = [] } = results;
  const lastOrder = tableOrders[0] || {};

  return (
    <div className={`live-result-card ${orderClass[lastOrder.type]}`}>
      <div className="m-x-4">
        <div className="flex space-btw">
          <div className="flex-25">
            <Skeleton keyName="Txn Type">{lastOrder.type}</Skeleton>
          </div>
          <div className="flex-25">
            <Skeleton keyName="Price">
              <Text>{lastOrder.price}</Text>
            </Skeleton>
          </div>
          <div className="flex-25">
            <Skeleton keyName="Quantity">
              {lastOrder.quantity}
            </Skeleton>
          </div>
          <div className="flex-25">
            <Skeleton keyName="Time">{lastOrder.datetime}</Skeleton>
          </div>
        </div>
        <div className="m-t-32">
          <Choose>
            <When
              condition={lastOrder.type === 'Buy' || lastOrder.type === 'Sell'}
            >
              <div className="fb-700 fb-1">
                {status[lastOrder.progressState]}
              </div>
              <Progress
                max={100}
                model="order.state"
                value={lastOrder.progressState}
              />
            </When>
            <Otherwise>
              <div className="empty">No orders to process.</div>
            </Otherwise>
          </Choose>
        </div>
      </div>
    </div>
  );
};

OrderStatus.propTypes = {
  results: PropTypes.shape({
    buyCount: PropTypes.number,
    charges: PropTypes.number,
    comboName: PropTypes.string,
    loseCount: PropTypes.number,
    orderCount: PropTypes.number,
    orders: PropTypes.arrayOf(PropTypes.shape({})),
    pnl: PropTypes.number,
    pnlBeforeExpense: PropTypes.number,
    sellCount: PropTypes.number,
    tableOrders: PropTypes.arrayOf(
      PropTypes.shape({ price: PropTypes.string })
    ),
    winCount: PropTypes.number,
    winPct: PropTypes.number,
  }),
};

OrderStatus.defaultProps = {
  results: [],
};
