import exponentialMovingAverage from '@multiverses/verse-utility/technicalIndicators/exponentialMovingAverage';

import { LiveResults } from '../../../component/LiveResults';

export const ema = [
  {
    name: 'EMA Up Down',
    description:
      'Runs EMA combos and gives the highest probability EMA for profit.',
    calculation: ({
      baseSet,
      valueKey = 'close',
      hasBuyOrder,
      buyOrder,
      sellOrder,
      processIndex = 0,
    }) => {
      const emas = [1, 2, 3, 5, 7, 9, 11, 14, 15, 17, 20];
      const emaValues = {};
      emas.forEach(ema => {
        const p1 = { datasets: baseSet, period: ema, valueKey };
        emaValues[ema] = exponentialMovingAverage(p1);
      });
      const emaCombos = [{ e1: 1, e2: 3 }];
      emaCombos.forEach(emaCombo => {
        const slicelength = emaCombo.e2 - emaCombo.e1;
        const ema1s = emaValues[emaCombo.e1].slice(
          slicelength,
          emaValues[emaCombo.e1].length
        );
        const ema2s = emaValues[emaCombo.e2];
        const emaLength = ema1s.length;
        let lastBuyPrice = 0;
        console.log('hasBuyOrder', hasBuyOrder);
        for (let i = processIndex; i < emaLength; i++) {
          /** Has No Buy Order, if fast ema beats slow ema, make a buy */
          console.log('EMA 1 ', i, ema1s[i].ema);
          console.log('EMA 2 ', i, ema2s[i].ema);
          if (!hasBuyOrder && ema1s[i].ema > ema2s[i].ema) {
            buyOrder(i, slicelength);
            return;
          } else if (
            hasBuyOrder &&
            (ema2s[i].ema > ema1s[i].ema ||
              lastBuyPrice >= baseSet[baseSet.length - 1].close + 2)
          ) {
            sellOrder(i, slicelength);
            return;
          }
        }
      });
    },
    component: () => (
      <div className="flex-75">
        <LiveResults result={[]} />
      </div>
    ),
  },
];
